@font-face {
  font-family: 'roboto-light';
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/roboto/roboto-300.woff2") format("woff2"), url("../fonts/roboto/roboto-300.woff") format("woff"); }

@font-face {
  font-family: 'roboto-bold';
  font-weight: 900;
  font-style: normal;
  src: url("../fonts/roboto/roboto-900.woff2") format("woff2"), url("../fonts/roboto/roboto-900.woff") format("woff"); }

@font-face {
  font-family: 'signika-bold';
  font-weight: 900;
  font-style: normal;
  src: url("../fonts/signika/signika-600.woff2") format("woff2"), url("../fonts/signika/signika-600.woff") format("woff"); }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: 'roboto-light';
  margin: 0; }

h1 {
  font-family: 'signika-bold'; }



